gsap.registerPlugin(ScrollTrigger);
gsap.registerPlugin(MotionPathPlugin);
Splitting();

const lenis = new Lenis({
  duration: 1.2,
  easing: (t) => Math.min(1, 1.001 - Math.pow(2, -10 * t)), // https://www.desmos.com/calculator/brs54l4xou
  direction: "vertical", // vertical, horizontal
  gestureDirection: "vertical", // vertical, horizontal, both
  smooth: true,
  mouseMultiplier: 1,
  smoothTouch: false,
  touchMultiplier: 2,
  infinite: false,
});

function raf(time) {
  lenis.raf(time);
  requestAnimationFrame(raf);
}

requestAnimationFrame(raf);

const links = document.querySelectorAll("a");
const main = document.querySelector("main");

// cursor
function cursor(e) {
  const mouse = document.querySelector(".cursor");
  mouse.style.top = e.pageY + "px";
  mouse.style.left = e.pageX + "px";
}

window.addEventListener("mousemove", cursor);

//page transitions
function loadScript(src, callback) {
  var script, scriptTag;
  script = document.createElement("script");
  script.type = "text/javascript";
  script.src = src;
  script.onload = script.onreadystatechange = function () {
    if (!this.readyState || this.readyState == "complete") {
      callback();
    }
  };
  scriptTag = document.querySelectorAll("script").slice(-1);
  // scriptTag.parentNode.insertBefore(script, sriptTag);
  console.log(scriptTag);
}

loadScript();
barba.init({
  views: [
    {
      namespace: "home",
      beforeEnter() {},
    },
  ],
  transitions: [
    {
      leave(data) {
        const done = this.async();

        const tl = gsap.timeline({
          defaults: {
            ease: "power2.inOut",
          },
        });

        tl.fromTo(data.current.container, 0.75, { opacity: 1 }, { opacity: 0 });
        tl.fromTo(".page", 0.75, { y: "-100%" }, { y: "0%" });
        tl.fromTo(
          ".page_content .word span",
          0.75,
          {
            y: "100%",
          },
          {
            y: "0%",
            stagger: 0.15,
            ease: "power2.inOut",
            onComplete: done,
          }
        );
      },
      enter(data) {
        window.scrollTo(0, 0);

        const done = this.async();

        const tl = gsap.timeline({
          defaults: {
            ease: "power2.inOut",
          },
        });

        tl.fromTo(
          ".page_content .word span",
          0.75,
          {
            y: "0%",
          },
          {
            y: "-100%",
            stagger: 0.15,
            ease: "power2.inOut",
            onComplete: done,
          }
        );
        tl.fromTo(".page", 0.75, { y: "0%" }, { y: "100%" });
        tl.fromTo(
          data.next.container,
          0.75,
          { opacity: 0 },
          {
            opacity: 1,
            // onComplete: () => {
            //   history.go(0);
            //   done;
            // },
          }
        );
        done();
      },
    },
  ],
});
